                    {{this.sobreSiteMim().toUpperCase()}}
                  </a>
                  <div v-if="myInfo" class="dropdown-menu" >
                    <a v-on:click="aboutMe" class="dropdown-item">{{this.sobreSiteMim()}}</a>
                    <a v-on:click="contact" class="dropdown-item">{{this.contatoText()}}</a>
                  </div>
